@font-face {
  font-family: 'MyFont';
  src: url('./my-font.woff2') format('woff2'),
    url('./my-font.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}

.hello {
  color: red;
  font-family: 'MyFont';
  background: url('./icon.png');
}

::placeholder {
  color: gray;
}

.image {
  background-image: url(./icon.png);
}
@media (min-resolution: 2dppx) {
  .image {
    background-image: url(./icon.png);
  }
}
